import * as CortexDecoder from 'codecorp-web_sdk';
import "../node_modules/codecorp-web_sdk/dist/web/3d564ff198da9d1b5f56b12d5e6527ea.wasm"
import './styles/styles.scss';
import { LitElement, html, css } from 'lit';


class CortexScanner extends LitElement {
    static properties = {
        who: {type: String},
      };
      
      // return a promise for contract changes.
      static getMetaConfig() {
        return {
          controlName: 'Cortex Scanner',
          fallbackDisableSubmit: false,
          version: '1.2',
          properties: {
            who: {
              type: 'string',
              title: 'Who',
              description: 'Who to say hello to'
            }
          }
        };
      }

    constructor() {
        super();
        this.isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
        this.who = 'World';
    }

  
  

  //Function that initializes the decoder and all the lists in the app
  async firstUpdated() {

  
      try {
          //Initialize the decoder
          await CortexDecoder.CDDecoder.init();
          CortexDecoder.CDDevice.audio = true;
  
          // //Activate the license
          console.log(await CortexDecoder.CDLicense.activateLicense("").catch(e => console.log(e)));
  
          //Initialize the camera
          await CortexDecoder.CDCamera.init(this.shadowRoot.getElementById("videoCanvas"));
  
          
          //Get available camera devices and create a dropdown for selection for non mobile platform
          this.getAvailableCameraDevices(CortexDecoder.CDCamera.getConnectedCameras())
  
          await this.initializeEventHandlers()
  
          //Initialize camera and start preview
          await this.initializeCameraPreview()
          
  
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Initializing events for button clicks
  async initializeEventHandlers(){
      this.shadowRoot.getElementById("cameradevices").addEventListener("change",(event)=>{this.switchCamera(event.target.value)})
      this.shadowRoot.getElementById("toggleCamera").addEventListener("click",(element, event)=>this.toggleCamera())
      this.shadowRoot.getElementById("toggleCameraPreview").addEventListener("click",(element, event)=>this.toggleCameraPreview())
      this.shadowRoot.getElementById("toggleDecoding").addEventListener("click",(element, event)=>xthis.toggleDecoding())
      this.shadowRoot.getElementById("zoomSlider").addEventListener("input",(element, event)=>this.setZoom())
      this.shadowRoot.getElementById("TorchtoggleButton").addEventListener("click",(element, event)=>this.toggleTorch())
      this.shadowRoot.getElementById("clearTable").addEventListener("click",(element, event)=>this.clearTable())
  
  }
  
  //Get camera permission, start camera and start the preview
  async initializeCameraPreview(){
      await this.setCamera(this.shadowRoot.getElementById("cameradevices").value)
      await this.startCamera()
      await this.startCameraPreview()
  }
  
  //Get a list of available camera devices
  getAvailableCameraDevices(cameraDevices) {
      try {
          let selectElement = this.shadowRoot.getElementById("cameradevices");
  
          for (let i of cameraDevices) {
             
              let opt = document.createElement("option");
              opt.value = i.label;
              opt.innerHTML = i.label;
              selectElement.appendChild(opt)
          }
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Print the decode results in the results table
  async drawTable(result) {
      
      try {
          if (Object.keys(result).length !== 0) {
          
              let table = this.shadowRoot.getElementById("resultTable");
              let tablebody = this.shadowRoot.getElementById("tableBody");
              var tableRow = document.createElement("tr");
              let resultString = document.createElement("td");
              let symbologyName = document.createElement("td");
              let decodeTime = document.createElement("td");
  
              resultString.innerHTML = result.barcodeData;
              symbologyName.innerHTML = result.symbology;
              decodeTime.innerHTML = result.decodeTime;
  
              tableRow.appendChild(resultString);
              tableRow.appendChild(symbologyName);
              tableRow.appendChild(decodeTime);
              tablebody.appendChild(tableRow);
  
          }
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Clear the results in the table
  clearTable() {
      try {
          this.shadowRoot.querySelector("tbody").parentNode.removeChild(this.shadowRoot.querySelector("tbody"));
          let tableBody = this.shadowRoot.createElement("tbody")
          tableBody.setAttribute("id", "tableBody")
          this.shadowRoot.getElementById("resultTable").appendChild(tableBody);
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  
  resetElements() {
      this.shadowRoot.getElementById("zoomSliderLabel").innerHTML = "Zoom not available";
      this.shadowRoot.getElementById("zoomSlider").hidden = true;
      this.shadowRoot.getElementById("TorchtoggleButton").value = 0;
      this.shadowRoot.getElementById("TorchtoggleButton").innerHTML = "Turn Flash ON";
      this.shadowRoot.getElementById("TorchtoggleButton").disabled = false;
      this.shadowRoot.getElementById("toggleCamera").value = 0;
      this.shadowRoot.getElementById("toggleCamera").innerHTML = "Start Camera";
      this.shadowRoot.getElementById("toggleCamera").style.backgroundColor = "#4CAF50";
      this.shadowRoot.getElementById("toggleCameraPreview").value = 0;
      this.shadowRoot.getElementById("toggleCameraPreview").innerHTML = "Start Camera Preview";
      this.shadowRoot.getElementById("toggleCameraPreview").style.backgroundColor = "#4CAF50";
      this.shadowRoot.getElementById("toggleDecoding").value = 0;
      this.shadowRoot.getElementById("toggleDecoding").innerHTML = "Start Decode";
      this.shadowRoot.getElementById("toggleDecoding").style.backgroundColor = "#4CAF50";
  }
  
  //Switch to a different camera
  async switchCamera(cameraLabel){
      await this.stopCamera()
      await this.setCamera(cameraLabel)
      await this.startCamera()
      await this.startCameraPreview()
  }
  
  //Set the desired camera device from the list of available cameras
  async setCamera(cameraLabel){
      console.log("Camera : "+cameraLabel)
      try{
          if (this.isMobile) {
              CortexDecoder.CDCamera.setCameraPosition("BACK")
          } else {
              await CortexDecoder.CDCamera.setCamera(cameraLabel)
          }
      }catch(err){
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Start the camera
  async startCamera() {
      try {
          await CortexDecoder.CDCamera.startCamera()
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Start the preview
  async startCameraPreview() {
      try {
          await CortexDecoder.CDCamera.startPreview((result) => {
              try {
                  if (result && result[0].barcodeData !== "") {
                      this.drawTable(result[0])
                  }
              } catch (err) {
                  if (this.isMobile) alert(err)
                  else console.log(err);   
              }
          });
          this.setZoomSlider()
         
  
          if (!CortexDecoder.CDCamera.isTorchSupported()) {
              this.shadowRoot.getElementById("TorchtoggleButton").disabled = true;
              this.shadowRoot.getElementById("TorchtoggleButton").innerHTML = "Flash not supported"
          } else {
              this.shadowRoot.getElementById("TorchtoggleButton").disabled = false;
              this.shadowRoot.getElementById("TorchtoggleButton").innerHTML = "Turn Flash ON"
          }
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Stop the preview and stop the camera
  async stopCamera() {
      try {
          await CortexDecoder.CDCamera.stopCamera();
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Stop the preview but hold the camera resource
  stopCameraPreview() {
      try {
          CortexDecoder.CDCamera.stopPreview();
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  
  toggleCamera() {
      try {
          let val = this.shadowRoot.getElementById("toggleCamera").value;
          if (val == 0) {
              this.startCamera()
              this.shadowRoot.getElementById("toggleCamera").value = 1;
              this.shadowRoot.getElementById("toggleCamera").innerHTML = "Stop Camera";
              this.shadowRoot.getElementById("toggleCamera").style.backgroundColor = "red";
          } else {
              this.stopCamera()
              this.resetElements()
          }
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  
  toggleCameraPreview() {
      try {
          let val = this.shadowRoot.getElementById("toggleCameraPreview").value;
          if (val == 0) {
              if (this.shadowRoot.getElementById("toggleCamera").value == 0) alert("Start Camera First")
              else {
                  this.startCameraPreview()
                  this.shadowRoot.getElementById("toggleCameraPreview").value = 1;
                  this.shadowRoot.getElementById("toggleCameraPreview").innerHTML = "Stop Camera Preview";
                  this.shadowRoot.getElementById("toggleCameraPreview").style.backgroundColor = "red";
                  this.toggleDecoding()
              }
          } else {
              this.stopCameraPreview()
              this.shadowRoot.getElementById("toggleCameraPreview").value = 0;
              this.shadowRoot.getElementById("toggleCameraPreview").innerHTML = "Start Camera Preview";
              this.shadowRoot.getElementById("toggleCameraPreview").style.backgroundColor = "#4CAF50";
              if (this.shadowRoot.getElementById("toggleDecoding").value = 1)
                  this.toggleDecoding()
          }
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Start decoding
  startDecoding() {
      try {
          CortexDecoder.CDDecoder.decoding = true
          // CortexDecoder.CDCamera.setVideoCapturing(true)
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Stop decoding
  stopDecoding() {
      try {
          CortexDecoder.CDDecoder.decoding = false
          // CortexDecoder.CDCamera.setVideoCapturing(false)
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  toggleDecoding() {
      try {
          let val = this.shadowRoot.getElementById("toggleDecoding").value;
          if (val == 0) {
              if (this.shadowRoot.getElementById("toggleCamera").value == 0) alert("Start Camera First")
              else if (this.shadowRoot.getElementById("toggleCamera").value == 1 && this.shadowRoot.getElementById("toggleCameraPreview").value == 0) alert("Start Camera Preview First")
              else {
                  this.startDecoding()
                  this.shadowRoot.getElementById("toggleDecoding").value = 1;
                  this.shadowRoot.getElementById("toggleDecoding").innerHTML = "Stop Decode";
                  this.shadowRoot.getElementById("toggleDecoding").style.backgroundColor = "red";
              }
          } else {
              this.stopDecoding()
              this.shadowRoot.getElementById("toggleDecoding").value = 0;
              this.shadowRoot.getElementById("toggleDecoding").innerHTML = "Start Decode";
              this.shadowRoot.getElementById("toggleDecoding").style.backgroundColor = "#4CAF50";
          }
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Set the zoom slider range
  setZoomSlider() {
      try {
          let zoomCapability = CortexDecoder.CDCamera.getSupportedZoomRange();
          if (!zoomCapability) return;
          this.shadowRoot.getElementById("zoomSliderLabel").innerHTML = "Adjust the zoom";
          let zoomSlider = this.shadowRoot.getElementById("zoomSlider");
          zoomSlider.min = zoomCapability.min;
          zoomSlider.max = zoomCapability.max;
          zoomSlider.step = zoomCapability.step;
          zoomSlider.hidden = false;
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  //Set the zoom value
  setZoom() {
      try {
          let zoomSlider = this.shadowRoot.getElementById("zoomSlider");
          if(CortexDecoder.CDCamera.isZoomSupported()){
              CortexDecoder.CDCamera.setZoom(parseFloat(zoomSlider.value));
          }else{
              CortexDecoder.CDCamera.setCrop(parseFloat(zoomSlider.value));
          }
      } catch (err) {
          if (this.isMobile) alert(err)
          else console.log(err);
      }
  }
  
  
  toggleTorch() {
      try {
          let val = this.shadowRoot.getElementById("TorchtoggleButton").value;
          if (val == 0) {
              if (this.shadowRoot.getElementById("toggleCamera").value == 1) {
                  CortexDecoder.CDCamera.setTorchMode(true)
                  this.shadowRoot.getElementById("TorchtoggleButton").value = 1;
                  this.shadowRoot.getElementById("TorchtoggleButton").innerHTML = "Turn Flash OFF";
              } else {
                  alert("You should start preview first to access flash")
              }
          } else {
              CortexDecoder.CDCamera.setTorchMode(false)
              this.shadowRoot.getElementById("TorchtoggleButton").value = 0;
              this.shadowRoot.getElementById("TorchtoggleButton").innerHTML = "Turn Flash ON";
          }
      } catch (err) {
          alert(err)
      }
  }

render() {
    return html`
    <div id="page-container">
    <header id="header">
        <div style = " text-align: center;" class="header-container">
        </div>
    </header>
    <div id="videoContainer">
        <div id="cameraSelector"><label for="cameradevices">Choose the camera device ver 2</label>
        <select name="cameradevices" id="cameradevices"></select></div>
        <div id="videoCanvasContainer" style="height: 500px;">
            <canvas id="videoCanvas" width="640" height="360" style="z-index: 0; position: absolute; margin-left: auto; margin-right: auto; left: 0; right: 0; text-align: center;"></canvas>
        </div>
        
        <!-- <video id="video" width="640" height="360" playsinline></video> -->
    </div>

    <div>
        <button class="button" id="toggleCamera"  style="background-color: red;" value=1>Stop Camera</button>
        <button class="button" id="toggleCameraPreview" style="background-color: red;" value=1>Stop Camera Preview</button>
        <button class="button" id="toggleDecoding" style="background-color: red;" value=1>Stop Decode</button>
    </div>


    <div style="padding: 20px;">
        <label id="zoomSliderLabel" for="zoomSlider">Zoom not available</label>
        <input id="zoomSlider" type="range" value="0" hidden>
    </div>


    <div style="padding: 20px;">
        <button style="background-color: darkkhaki;" class="button" id="TorchtoggleButton" value=0>Turn Flash ON</button>
    </div>


    <div id="div3" style="display: inline-table; margin-top: 30px;">
        <table id = "resultTable">
            <thead>
                <tr>
                    <th>
                        RESULT STRING
                    </th>
                    <th>
                        SYMBOLOGY
                    </th>
                    <th>
                        TIME TAKEN
                    </th>
                    
                </tr>
            </thead>
            <tbody id="tableBody">
                
            </tbody>
        </table>
    </div>
    <div style="display: inline-block;"><button id = "clearTable">Clear Table</button></div>
</div>
    `;
  }
}
// registering the web component
const elementName = 'cortex-scanner';
customElements.define('cortex-scanner', CortexScanner);

