___CSS_LOADER_EXPORT___.push([module.id, `#page-container {
  position: relative;
  min-height: 100vh;
  background-color: #ffffff;
  text-align: center;
}

header {
  background-color: #e1e1e1;
  padding: 30px;
}

table {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  margin-bottom: 300px;
}

#resultTable th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #414042;
  color: white;
}

#resultTable td, #resultTable th {
  border: 1px solid #ddd;
  padding: 8px;
}

#showVideo, #div3 {
  display: block;
  text-align: center;
}

#showVideo {
  margin-left: 45%;
}

#videoContainer {
  padding-top: 50px;
}

#getPreviewButton {
  margin-right: 2vw;
  margin-bottom: 2vw;
}

#startVideoCapturingButton {
  margin-right: 2vw;
  margin-bottom: 2vw;
}

input[type=range] {
  width: 400px;
}

.sticky-sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.dropdown-btn.active {
  background-color: #666;
  color: white;
}

/* The side navigation menu */
.sidenav {
  height: 100%; /* 100% Full-height */
  width: 0; /* 0 width - change this with JavaScript */
  position: fixed; /* Stay in place */
  z-index: 1; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  background-color: #e1e1e1; /* Black*/
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 60px; /* Place content 60px from the top */
  transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */
}

/* The navigation menu links */
.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

/* When you mouse over the navigation links, change their color */
.sidenav a:hover .dropdown-btn:hover {
  color: #f1f1f1;
}

/* Position and style the close button (top right corner) */
.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

.dropdown-container {
  display: none;
  /* background-color: #262626; */
  padding-left: 8px;
}

.dropdown-btn {
  /* display: block; */
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
  transition: 0.3s;
}`, "",{"version":3,"sources":["webpack://./src/styles/styles.scss"],"names":[],"mappings":"AAAA;EACI,kBAAA;EACA,iBAAA;EACA,yBAAA;EACA,kBAAA;AACJ;;AAEA;EACI,yBAAA;EACA,aAAA;AACJ;;AAEA;EACI,yDAAA;EACA,yBAAA;EACA,oBAAA;AACJ;;AAEA;EACI,iBAAA;EACA,oBAAA;EACA,kBAAA;EACA,yBAAA;EACA,YAAA;AACJ;;AAEA;EACI,sBAAA;EACA,YAAA;AACJ;;AAEA;EACI,cAAA;EACA,kBAAA;AACJ;;AAEA;EACI,gBAAA;AACJ;;AAEA;EACI,iBAAA;AACJ;;AAEA;EACI,iBAAA;EACA,kBAAA;AACJ;;AAEA;EACI,iBAAA;EACA,kBAAA;AACJ;;AAEA;EACI,YAAA;AACJ;;AAEA;EACI,wBAAA;EACA,gBAAA;EACA,MAAA;AACJ;;AAEA;EACI,yBAAA;EACA,YAAA;EACA,YAAA;EACA,kBAAA;EACA,kBAAA;EACA,qBAAA;EACA,qBAAA;EACA,eAAA;EACA,eAAA;EACA,eAAA;AACJ;;AAEA;EACI,sBAAA;EACA,YAAA;AACJ;;AAEA,6BAAA;AACA;EACI,YAAA,EAAA,qBAAA;EACA,QAAA,EAAA,0CAAA;EACA,eAAA,EAAA,kBAAA;EACA,UAAA,EAAA,gBAAA;EACA,MAAA,EAAA,oBAAA;EACA,OAAA;EACA,yBAAA,EAAA,SAAA;EACA,kBAAA,EAAA,8BAAA;EACA,iBAAA,EAAA,oCAAA;EACA,gBAAA,EAAA,yDAAA;AACJ;;AAEE,8BAAA;AACA;EACE,yBAAA;EACA,qBAAA;EACA,eAAA;EACA,cAAA;EACA,cAAA;EACA,gBAAA;AACJ;;AAEE,iEAAA;AACA;EACE,cAAA;AACJ;;AAEE,2DAAA;AACA;EACE,kBAAA;EACA,MAAA;EACA,WAAA;EACA,eAAA;EACA,iBAAA;AACJ;;AAGE;EACA,aAAA;EACA,+BAAA;EACA,iBAAA;AAAF;;AAGA;EACI,oBAAA;EACA,yBAAA;EACA,qBAAA;EACA,eAAA;EACA,cAAA;EACA,YAAA;EACA,gBAAA;EACA,eAAA;EACA,aAAA;EACA,gBAAA;AAAJ","sourcesContent":["#page-container {\r\n    position: relative;\r\n    min-height: 100vh;\r\n    background-color:#ffffff;\r\n    text-align: center;\r\n}\r\n\r\nheader {\r\n    background-color:#e1e1e1;\r\n    padding: 30px;\r\n}\r\n\r\ntable{\r\n    font-family: \"Trebuchet MS\", Arial, Helvetica, sans-serif;\r\n    border-collapse: collapse;\r\n    margin-bottom: 300px;\r\n}\r\n\r\n#resultTable th {\r\n    padding-top: 12px;\r\n    padding-bottom: 12px;\r\n    text-align: center;\r\n    background-color: #414042;\r\n    color: white;\r\n}\r\n\r\n#resultTable td, #resultTable th {\r\n    border: 1px solid #ddd;\r\n    padding: 8px;\r\n}\r\n\r\n#showVideo, #div3{\r\n    display: block;\r\n    text-align: center;\r\n}\r\n\r\n#showVideo{\r\n  